* { 
    margin: 0px; 
    padding: 0px; 
    box-sizing: border-box; 
} 
 
.common { 
    width: 40%; 
    height: 300px; 
    background-color: black; 
    color: red; 
    box-shadow: 4px 4px 2px 2px yellow; 
    border-radius: 10%; 
    display: flex; 
    justify-content: center; 
    align-items: center; 
    font-size: x-large; 
    display: none;  
} 
 

 
.block { 
    width: 40%; 
    height: 300px; 
    background-color: black; 
    color: red; 
    box-shadow: 4px 4px 2px 2px yellow; 
    border-radius: 10%; 
    display: flex; 
    justify-content: center; 
    align-items: center; 
    font-size: x-large; 
    display: block; 
    animation-name: example; 
    animation-duration: 4s; 
    animation-iteration-count: infinite;  
} 
 
@keyframes example { 
    0%   {background-color: red;} 
    25%  {background-color: yellow;} 
    50%  {background-color: blue;} 
    100% {background-color: green;} 
  }